/*=================================================
// #3 Theme default style
=================================================*/
html {
    -webkit-overflow-scrolling: touch;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 19px;
    scroll-behavior: smooth;

    @media #{$media_xxl} {
        font-size: 17.5px;
    }

    @media #{$media_xl} {
        font-size: 16px;
    }

    @media #{$media_sm} {
        font-size: 14px;
    }
}

body {
    background-color: $body_background_color;
    font-size: 19px;
    min-width: 320px;

    @media #{$media_xxl} {
        font-size: 17.5px;
    }

    @media #{$media_xl} {
        font-size: 16px;
    }

    @media #{$media_sm} {
        font-size: 14px;
    }
}

::selection {
    background: $body_main_element_primary_color;
    color: $white;
}

.wrapper,
body:not(.single-post)[class*='-full-width'] [class*='-section'] [class*='section-boxed'] > [class*='-container'] {
    max-width: $wrapper_max_w;
    margin: auto;
    padding-left: $wrapper_p_l;
    padding-right: $wrapper_p_r;

    @media #{$media_xxl} {
        padding-left: $wrapper_p_l_xxl;
        padding-right: $wrapper_p_r_xxl;
    }

    @media #{$media_xl} {
        padding-left: $wrapper_p_l_xl;
        padding-right: $wrapper_p_r_xl;
    }

    @media #{$media_sm} {
        padding-left: $wrapper_p_l_sm;
        padding-right: $wrapper_p_r_sm;
    }
}

